@import "../../vars.css";

@component MediaListRow {
  height: 54px;
  line-height: 54px;
  position: relative;
  background: $media-list-color;

  @modifier alternate {
    background: $media-list-alternate-color;
  }

  @when loading {
    opacity: 0.75;
  }

  @when selected {
    background: color($highlight-color alpha(* 70%));
  }

  @descendent loader {
    margin: 0 15px;
  }

  @descendent thumb {
    height: 100%;
    width: 55px;
    margin: 0 15px;
    padding: 7px 0;
    float: left;
  }

  @descendent image {
    max-width: 100%;
    max-height: 100%;
  }

  @descendent artist {
    height: 100%;
    float: left;
    width: calc(50% - $media-list-spacing - $media-list-thumb-width - $media-list-duration-width);
    margin-right: $media-list-spacing;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  @descendent title {
    height: 100%;
    float: left;
    width: calc(50% - $media-list-spacing);
    margin-right: $media-list-spacing;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  @descendent duration {
    height: 100%;
    float: left;
    width: $media-list-duration-width;
  }

  @descendent actions {
    height: 100%;
    position: absolute;
    right: 0;
  }
}
